import React, { Component, useState } from 'react';
import { Table, Input, InputNumber, Button, Form, Modal, message, Row, Col, Menu, Select } from 'antd';
import { EditOutlined, DeleteOutlined, PlusCircleOutlined, ExclamationCircleOutlined } from '@ant-design/icons';
import Flex from 'components/shared-components/Flex'
import { DataService } from "services/loanPayments.services";
import { Typography } from "antd";
import EllipsisDropdown from 'components/shared-components/EllipsisDropdown';
import NumberFormat from 'react-number-format';

const { Title } = Typography;
const { confirm } = Modal;
const { Option } = Select;

const EditableTable = ({systemData}) => {
  const [form] = Form.useForm();
  const [editingKey, setEditingKey] = useState('');
  const [visible, setVisible] = useState(false);
  const [confirmLoading, setConfirmLoading] = useState(false);

  const { loans, loansLoading, loanPayments, loanPaymentsLoading, allLoans, allLoansLoading } = systemData;

  const viewDetails = row => {
    
    setEditingKey(row._id);
    setVisible(true);
    form.setFieldsValue({
      loanId:row.loanId,
      amount:row.amount,
    });
  }

  const dropdownMenu = row => (
		<Menu>
			<Menu.Item onClick={() => {viewDetails(row)}}>
				<Flex alignItems="center">
					<EditOutlined />
					<span className="ml-2">Edit Details</span>
				</Flex>
			</Menu.Item>
			<Menu.Item onClick={() => confirmDelete(row)}>
				<Flex alignItems="center">
					<DeleteOutlined />
					<span className="ml-2">Delete</span>
				</Flex>
			</Menu.Item>
		</Menu>
	);

  const confirmDelete = (row) => {
    confirm({
      title: 'Do you Want to delete this loan payment?',
      icon: <ExclamationCircleOutlined />,
      onOk() {
        remove(row._id);
      }
    });
  };
	

  const showModal = () => {
    setVisible(true);
  };

  const handleOk = async e => {
    try {
      const values = await form.validateFields();
      let loanId = values.loanId.slice(0,1) === ' ' ? values.loanId.slice(1) : values.loanId;
      let amount = values.amount;

      if (loanId =='' || amount == '') return;

      setConfirmLoading(true);
      let paymentData = { loanId,amount}
      if(!editingKey) DataService.create(paymentData);
      else{
        DataService.update(editingKey,paymentData)
        setEditingKey('');
        message.success(`Loan payment information updated`);
      }
      handleCancel();
    } catch (errInfo) {}
    
  };

  const handleCancel = () => {
    setVisible(false);
    setConfirmLoading(false);
    form.setFieldsValue({loanId:null,amount:null});
  };
  
  const remove = key => {
    DataService.remove(key)
    message.success(`Loan payment information have been deleted`);
  };

  const columns = [
    {
      title: 'Date',
      dataIndex: 'date',
      render: (_,record) => new Date(+record.data()._id).toLocaleString()
    },
    {
      title: 'Loan',
      dataIndex: 'loanId',
      render: (_, record) => {
        return allLoansLoading ? '' : (
          <div>
					<NumberFormat
						displayType={'text'} 
            prefix={allLoans[record.data().loanId]?.purpose+' - '}
						value={allLoans[record.data().loanId]?.amount}
            suffix={' - '+allLoans[record.data().loanId]?.bankName}
						thousandSeparator={true} 
					/>
				</div>)
      }
    },
    {
      title: 'Amount',
      dataIndex: 'amount',
      render: (_, record) => (
				<div>
					<NumberFormat
						displayType={'text'} 
						value={record.data().amount} 
						thousandSeparator={true} 
					/>
				</div>
			),
    },
    {
			title: '',
			dataIndex: 'actions',
			render: (_, elm) => (
				<div className="text-right">
					<EllipsisDropdown menu={dropdownMenu(elm.data())}/>
				</div>
			)
		}
  ];
  return (
    <Form form={form} component={false} layout="vertical">
      <Flex alignItems="center" justifyContent="between" mobileFlex={false}>
        <Flex className="mb-1" mobileFlex={false}>
          <div className="mr-md-3">
            <Title level={4}>Loans Payments List</Title>
          </div>
        </Flex>
        <Flex mobileFlex={false}>
          <div>
            <Button size={'small'} 
                    style={{
                      marginTop: 0,
                      paddingTop: 0,
                    }}
                    onClick={showModal}
                    type="primary" icon={<PlusCircleOutlined />} block>Add New Payment</Button>
            <Modal
              title={editingKey ? "Edit Loan Payment details" : "New Loan Payment"}
              visible={visible}
              onOk={handleOk}
              confirmLoading={confirmLoading}
              onCancel={handleCancel}
            >
              <Row gutter={16}>
                <Col xs={24} sm={24} md={24}>
                  <Form.Item name="loanId" label="Loan paid" rules={[
                    {
                      required: true,
                      message: `loan is required.`,
                    },
                  ]}>
                    <Select
                        className="w-100"
                        showSearch
                        placeholder="Select which loan paid"
                        optionFilterProp="children"
                      >
                      {!loansLoading && loans.docs.map((loan, index) => (
                        <Option key={index} value={loan.data()._id}>
                          {loan.data().bankName+' - '+loan.data().amount+'. Purpose: '+loan.data().purpose}
                        </Option>
                      ))}
                    </Select>
                  </Form.Item>
                </Col>
                
                <Col xs={24} sm={24} md={24}>
                  <Form.Item name="amount" label="Amount Paid" rules={[
                    {
                      required: true,
                      message: `amount paid is required.`,
                    },
                  ]}>
                    <InputNumber
                      className="w-100"
                      min={0}
                      formatter={value => `${value}`.replace(/\B(?=(\d{3})+(?!\d))/g, ',')}
                      parser={value => value.replace(/\$\s?|(,*)/g, '')}
                    />
                  </Form.Item>
                </Col>

              </Row>
            </Modal>         
          </div>
        </Flex>
      </Flex>
      <Table
        dataSource={loanPaymentsLoading ? [] : loanPayments.docs.reverse()} 
        columns={columns}
        loading={loanPaymentsLoading}
        rowClassName="editable-row"
        size="middle"
      />
    </Form>
  );
};

export class Stores extends Component {
  render() {
    return (
      <EditableTable systemData={this.props.systemData}/>
    )
  }
}

export default Stores
